import React from 'react';
import s from './ProfileInfo.module.css'
import userPhoto from '../../../assess/images/images.png';
import ProfileInfoStatus from "./ProfileInfo.Status";


const  ProfileInfo = React.memo(({profile,updateStatus,status }) => {


    return (
        profile && <div>
            <div className={s.descriptionBlock}>
                <img src={profile.photos.large ? profile.photos.large : userPhoto}/>
            </div>
            <ProfileInfoStatus status ={status}   updateStatus ={updateStatus} />
            <div>{profile.aboutMe  }</div>
            <div>{profile.lookingForAJob }</div>
            <div>{profile.lookingForAJobDescription }</div>
            <div>{profile.fullName }</div>
            <div>{profile.contacts.facebook}</div>
        </div>
    )
})

export default ProfileInfo;


