import { usersAPI } from "../api/api";
import {updateObjectInArray} from "../utils/object_helper";
const FOLLOW = 'user_reducer/FOLLOW';
const UNFOLLOW = 'user_reducer/UNFOLLOW';
const SET_USERS = 'user_reducer/SET_USERS';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'user_reducer/TOGGLE_IS_FOLLOWING_PROGRESS';

let initialState = {
    users : [],
    followingIsProgress : []
}

const userReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId,'id', {followed: true})
            }
        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId,'id', {followed: false})
            }
        case SET_USERS:
            return {
                ...state,
                users: [...action.users]
            }
        case TOGGLE_IS_FOLLOWING_PROGRESS:
            return {
                ...state,
                followingIsProgress: action.isFetching ?
                    [...state.followingIsProgress, action.userId]
                    : state.followingIsProgress.filter(id => id !== action.userId)
            }
                default:
                return state;
            }

    }

export const followSuccess = (userId) => ({ type: FOLLOW, userId })
export const unfollowSuccess = (userId) => ({ type: UNFOLLOW, userId })
export const setUsers = (users) => ({ type: SET_USERS, users })
export const toggleIsFollowingProgress = (isFetching, userId) => ({ type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, userId })

 const followUnfollowSuccess = (dispatch, userId, apiMethod, actionCreator) => async (dispatch) => {
    dispatch(toggleIsFollowingProgress(true, userId));
    let response = await apiMethod(userId);
    if (response.data.resultCode === 0){
        dispatch (actionCreator(userId));
    }
    dispatch(toggleIsFollowingProgress(false, userId));
}

export const follow = (userId) => {
    let apiMethod = usersAPI.follow.bind(usersAPI)
    return async (dispatch) => {
        followUnfollowSuccess(dispatch, userId, apiMethod, followSuccess);
    }
}

export const unfollow = (userId) => {
    let apiMethod = usersAPI.unfollow.bind(usersAPI)
    return  async (dispatch) => {
        followUnfollowSuccess(dispatch, userId, apiMethod,  unfollowSuccess);
    }
}

export default userReducer;