import React, {useState} from 'react';
import MyInput from "../../../UI/input/MyInput";
import MyButton from "../../../UI/button/MyButton";

const PostForm = React.memo(({create}) => {
    const [post, setPost] = useState({title: '' , body:' '})
    function addNewPost(e) {
        e.preventDefault();
      const newPost = {
         ...post, likesCount:0, id: Date.now()
      }
      create(newPost)
        setPost({title: '', body: ''})
    }
    return (
        <div>
            <form>
               <div>
                   <MyInput  value={post.title} onChange={e => setPost({...post, title: e.target.value})} type='text'
                             placeholder='enter your post'/>
               </div>
               <div>
                   <MyInput  value={post.body} onChange={e => setPost({...post, body: e.target.value})} type='text'
                             placeholder='description'/>
               </div>
            </form>
            <div>
                <MyButton  className="btn btn-primary btn-" onClick={addNewPost}>add NewPost</MyButton>
            </div>
        </div>
    );
});

export default PostForm;
