import React from 'react';
import './FormikControl.css'
import {connect} from "react-redux";
import FormikLogin from "./FormikLogin";
import {login} from "../../redux/auth_reducer";
import {getIsAuth} from "../../redux/formikLogin_selector";

function FormikLoginContainer ({login , isAuth}) {

    return (
        <FormikLogin login={login} isAuth={isAuth}/>
    );
};

const mapStateToProps = (state) => {
    return (

        {
            isAuth: getIsAuth(state),
        }

    )
}

export default connect(mapStateToProps, {login})(FormikLoginContainer);
