import React, {useEffect, useState} from 'react';
import PostList from "./postList/PostList";
import PostForm from "./PostForm";
import s from './MyPosts.module.css'
import '../MyPosts/postList/PostList.css'
import PostFilter from "./PostFilter";
import MyModal from "../../../UI/myModal/MyModal";
import MyButton from "../../../UI/button/MyButton";
import {usePosts} from "../../hooks/usePosts";
import Preloader from "../../Preloader/Preloader";
import PostServiceApi from "../../../api/api";
import {useFetching} from "../../hooks/useFetching";
import {getPagesCount} from "../../../utils/pages";
import Pagination from "../../../UI/pagination/Pagination";


const MyPosts = () => {
    const [posts, setPosts] = useState([])
    const [filter, setFilter] = useState({sort: '', query: ''})
    const [myModal, setMyModal] = useState(false)
    const sortedAndSearchedPosts = usePosts(posts, filter.sort, filter.query)
    const [totalPages, setTotalPages] = useState(0)
    const [limit, setLimit] = useState(4)
    const [page, setPage] = useState(1)
    const [fetchPosts, isPostLoading, postError] = useFetching(async () => {
        const response = await PostServiceApi.getAllPost(limit, page)
        const totalCount = response.headers['x-total-count']
        setTotalPages(getPagesCount(totalCount, limit))
        setPosts(response.data)
    })

    const createPost = (newPost) => {
        setPosts([...posts, newPost])
        setMyModal(false)
    }
    const removePost = (post) => {
        setPosts(posts.filter(p => p.id != post.id))
    }
    useEffect(() => {
        fetchPosts()
    }, [page])
    const onPageChange = (page) => {
        setPage(page)
        setLimit(4)
    }

    return (

        <div className={s.messages}>
            <MyButton style={{marginTop: '25px '}} onClick={() => setMyModal(true)}> create post </MyButton>

            <MyModal visible={myModal} setVisible={setMyModal}>
                <PostForm create={createPost}/>
            </MyModal>
            <PostFilter filter={filter} setFilter={setFilter}/>
            <Pagination
                totalPages={totalPages}
                page={page}
                onPageChange={onPageChange}/>
            {postError &&
                <h1>Happen error {postError}</h1>
            }
            {isPostLoading
                ? <div style={{display: ' flex', justifyContent: 'center'}}><Preloader/></div>
                : <PostList posts={sortedAndSearchedPosts} remove={removePost}/>
            }
        </div>
    )

}


export default MyPosts;
