import React, {useState} from 'react';
import '../../components/Profile/MyPosts/postList/PostList.css'
import MyButton from "../button/MyButton";

const Pagination = ({totalPages, page, onPageChange, portionSize =8, }) => {

        let pages =[]
        for (let i=0; i < totalPages; i++){
            pages.push(i+1)
        }

    let portionCount = Math.ceil(totalPages/portionSize)
    let [portionNumber, setPortionNumber] = useState (1)
    let rightPortionPageNumber = portionNumber * portionSize
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1
    return (
        <div className ='pageWrapper'>
            {
                portionNumber > 1 &&
                <button style ={{paddingLeft: '1px', textAlign: 'center'}} onClick={ () => {setPortionNumber (portionNumber -1)} }> prev </button>
            }
            {pages
                .filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber )
                .map(p =>
                <div key = {p} onClick={
                    (e) => onPageChange(p)}
                     className ={ page === p ? 'page pageCurrent' : 'page' } >{p}</div>
            )}
            {
                portionCount > portionNumber &&
                <MyButton onClick ={ () => {setPortionNumber(portionNumber  + 1) }} > next </MyButton>
            }
        </div>
    );
};

export default Pagination;
