import React from 'react';
import s from './Post.module.css'
import MyButton from "../../../../UI/button/MyButton";


const Post = React.memo(({p, remove}) => {
    return (
        <div className={s.post}>

            <img
                src='https://avatars.mds.yandex.net/get-zen_doc/3381150/pub_5fe595608380f70f48b64390_5fe59ece8380f70f48c5d6e3/scale_1200'/>
            <div className={s.postDescription}>
                <div>
                    <strong> {p.id}. {p.title}</strong>
                </div>
                <div>
                    {p.body}
                </div>
            </div>

            <span> like {p.likesCount} </span>
            <div className ={s.btnItem}>
                <MyButton onClick={() => remove(p)}>remove</MyButton>
                <MyButton> open </MyButton>

            </div>
        </div>

    )
}
)
export default Post;
