import * as axios from "axios";

const instance = axios.create({
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    withCredentials: true,
    headers: {'API-KEY': 'e0cb3e4e-32a9-4730-a511-0ec6d21ef53b'}
})

export const profileAPI = {
    getProfile(userId) {
        return instance.get(`profile/` + userId)},
    getStatus(userId) {
        return instance.get(`profile/status/` + userId);
    },
    updateStatus(status) {
        debugger
        return instance.put('profile/status/', {status})
    }

}
export const authAPI = {
    authMe() {
        return instance.get('auth/me' )},
    login(email, password, rememberMe ) {
        return instance.post('auth/login', {email, password, rememberMe})
    },
    logout() {
        return instance.delete('auth/login')
    }

}
export const usersAPI = {
   getUsers  (page =1, limit=100) {
        return instance.get(`users?page=${page}&count=${limit}`)},
    getPageUsers  (page =1, limit=100) {
        return instance.get(`users?page=${page}&count=${limit}`)
    },
    follow (userId) {
       return instance.post(`follow/${userId}`,{})},
    unfollow  (userId) {
       return instance.delete(`follow/${userId}`)},
}


export default class PostServiceApi {
    static async getAllPost(limit= 20, page=1){
        const response = await axios.get( 'https://jsonplaceholder.typicode.com/posts',{
            params: {
                _limit: limit,
                _page: page
            }
        } )
        return response
    }
}



