import React, {useEffect} from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import AppRouter from "./components/AppRouter/AppRouter";
import HeaderContainer from "./components/Header/HeaderContainer";
import {compose} from "redux";
import {connect} from "react-redux";
import {getIsAuth} from "./redux/auth_selector";
import FormikLoginContainer from "./components/Login/FormikLoginContainer";
import Preloader from "./components/Preloader/Preloader";
import {getInitializeApp} from "./redux/app_selector";
import {initializeApp} from "./redux/app_reducer";

const App = React.memo(({isAuth, initialized,initializeApp}) => {
    useEffect(() => {
        initializeApp()
    }, [])

    if(!initialized) {return <Preloader />}
    return (
            <div className='app-wrapper'>
                {
                    initialized && isAuth ?   <Navbar />  : <FormikLoginContainer />
                }

                {
                    initialized && isAuth && <AppRouter />
                }

                <HeaderContainer />
            </div>
    );
})

let mapStateToProps = (state) => {
    return (
        {
           isAuth: getIsAuth(state),
            initialized: getInitializeApp(state)
        }
    )
}

export default compose (connect (mapStateToProps,
    { initializeApp })(App))


